import React from 'react'
import classNames from 'classnames/bind'
import Banner from '@/components/banner/Banner'
import { useWebpImg } from '@/utils/util'
import homeCss from '@/views/home/home.module.scss'
import scss from './sports.module.scss'

const cx = classNames.bind(scss)

export default function Live() {
  const list = [
    {
      name: '沙巴体育',
      img: require('@/views/sports/img/sb.png' + useWebpImg())
    }, {
      name: 'AG国际体育',
      img: require('@/views/sports/img/agin.png' + useWebpImg())
    }
  ]
  const swiperParams = {
    loop: false,
    pagination: {}
  }

  return (
    <div className={cx('sports')}>
      <div className={cx('sports__banner', 'default-bg')}>
        <Banner swiperParams={swiperParams} field="sports"/>
      </div>
      <div className={cx('sports__content', 'clearfix')}>
        {
          list.map((item, index) => (
            <div key={index.toString()} className={cx('sports__item')}>
              <img src={item.img} alt=""/>
              <div className={cx('sports__info')}>
                <p className={cx('sports__title')}>{item.name}</p>
                <button className={cx('sports__button', homeCss.home__button)}>游客参观</button>
              </div>
            </div>
          ))
        }
      </div>
    </div>
  )
}
